var util = {
	_pre:"vrtob",
	is_weixin:function() {
		var ua = navigator.userAgent.toLowerCase();
	    if(ua.match(/MicroMessenger/i)=="micromessenger") {
	        return true;
	    } else {
	        return false;
	    }
	},
	getUid() {
		var uid = this.get('uid')
		if(uid==null) {
			uid = this.createUid();
			this.set('uid',uid);
			return uid;
		} else {
			return uid;
		}
	},
	createUid() {
        var timestamp = Date.parse(new Date());
        var hashKey = parseInt(timestamp/1000);
        var rand = Math.random().toString(36).substring(7)
        var ua = navigator.userAgent.toLowerCase();
        var uid = rand+this.getHashCode(ua,hashKey)+hashKey
       	return uid
	},
	set:function(a,b) {
		 window.localStorage.setItem(this._pre + a, b);
	},
	get:function(a) {
		return window.localStorage.getItem(this._pre + a);
	},
	getHashCode:function(str,hashKey){
    	str = str.toLowerCase();
	    var hash  =  hashKey,i,ch;
	    for (i = str.length - 1; i >= 0; i--) {
	        ch = str.charCodeAt(i);
	        hash ^= ((hash << 5) + ch + (hash >> 2));
	    }
	    return  (hash & 0x7FFFFFFF);
	}
}
export default util


